<template lang="pug">
    .scholars__content
        .scholars__title Ученики
        .scholars__filter
            .scholars__filter-item
                label.label__filter Выберите департамент:
                select(@change="onChange()" v-model="selectedDepartment").scholars__filter-select
                    option(value='') Все
                    option(:value='item.name' v-for='item in departments') {{item.name}}
            .scholars__filter-item
                label.label__filter Выберите предмет:
                select(v-model="selectedSubject").scholars__filter-select
                    option(value='') Все
                    template(v-for='department in departments' v-if="selectedDepartment===department.name")
                        option(:value='subject' v-for='subject in department.subjects.slice().sort()'  v-if="selectedDepartment!==''") {{subject}}
                    template(v-for='department in departments'  v-if="selectedDepartment===''")
                        option(:value='item' v-for='item in department.subjects') {{item}}
            .scholars__filter-item
                label.label__filter(for='classes') Выберите класс:
                select(name='classes' v-model="selectedClass").scholars__filter-select
                    option(:value='0') Все
                    option(:value='item' v-for='item in 12') {{item}}
            .scholars__filter-item
                label.label__filter Выберите группу:
                select(v-model="selectedGroup").scholars__filter-select
                    option(value='') Все
                    template(v-for='department in departments' v-if="selectedDepartment===department.name")
                        option(:value='group' v-for='group in department.groups.slice().sort()'  v-if="selectedDepartment!==''") {{group}}
                    template(v-for='department in departments'  v-if="selectedDepartment===''")
                        option(:value='item' v-for='item in department.groups.slice().sort()') {{item}}
        .scholars__container
            ul.scholars__list
                li.scholars__item(v-for="scholar,idx in filteredScholars.slice().sort((a, b) => (a.lastname > b.lastname) ? 1 : (a.lastname === b.lastname) ? ((a.name > b.name) ? 1 : -1) : -1 )" :key="scholar.id")
                    scholar-item(
                        :index='idx'
                        :scholar='scholar'
                    )
        button(@click.prevent='addScholar' id='addScholar').add__scholar Добавить ученика
        add-scholar-form(
            v-if='addScholarFormVisible'
            v-on:eventNewScholar='eventNewScholar'
            )
</template>
<script>
export default {
    components: {
        addScholarForm: () => import("../../admin/scholars/add-scholar-form"),
        scholarItem: () => import("../../admin/scholars/scholar-item")
    },
  data () {
    return {
        addScholarFormVisible: false,
        departments: [
            {
                name: 'Cпорт и туризм',
                subjects: [
                    'Волейбол',
                    'Баскетбол',
                    'Футбол'
                ], 
                teachers: [
                    'Жусупова Динара', 'Таспаганбетова Жанар', 'Касымова Райхан'
                ],
                groups: [
                    '51VOLEYBALL-01-0001-G', '51FOOTBALL-01-0002-G'
                ]
            },
            {
                name: 'Лингвистика',
                subjects: [
                    'Казахский язык',
                    'Русский язык',
                    'Английский язык'
                ],
                teachers: [
                    'Жусупова Ботагоз', 'Таспаганбетова Анар', 'Касымов Арыстан'
                ],
                groups: [
                    '51KAZ-01-0001-G', '51RUS-01-0002-G'
                ]
            },
            {
                name: 'Вокал',
                subjects: [
                    'Хор',
                    'Соло'
                ],
                teachers: [
                    'Жусупова Анара', 'Таспаганбетов Жанат', 'Касымова Жанара'
                ],
                groups: [
                    '21SOLO-01-0001-I', '32CHOR-01-0002-G', '51MUSIC-01-0002-G'
                ]
            },
            {
                name: 'Естественно-технический',
                subjects: [
                    'Математика',
                    'Логика',
                    'География',
                    'Физика'
                ],
                teachers: [
                    'Жусупова Гульнара', 'Таспаганбетова Жанат', 'Касымов Турар'
                ],
                groups: [
                    '51MATH-01-0001-I', '51LOGIC-01-0002-G'
                ]
            }   
        ],
        selectedDepartment: '',
        selectedGroup: '',
        selectedSubject: '',
        selectedClass: 0,
        scholars: [
            {
                iin: '777777777777',
                name: 'Динара',
                lastname: 'Жусупова',
                school: 'Профильная школа #37',
                class: 5,
                status: 'active',
                departments: [
                    'Естественно-технический'
                ],
                groups: [
                    {
                        groupCode: '51MATH-01-0001-I',
                        groupSubject: 'Математика'
                    }, 
                                        {
                        groupCode: '51LOGIC-01-0002-G',
                        groupSubject: 'Логика'
                    }
                ]
            },
            {
                iin: '777777777777',
                name: 'Райхан',
                lastname: 'Касымова',
                school: 'Гуманитарный лицей #22',
                class: 7,
                status: 'active',
                groups: [
                    {
                        groupCode: '51GEO-01-0001-I',
                        groupSubject: 'География'
                    }, 
                                        {
                        groupCode: '51MUSIC-01-0002-G',
                        groupSubject: 'Соло'
                    }     
                ],
                departments: [
                    'Естественно-технический', 'Вокал'
                ],
            }
        ]
    }
  }, 
  computed: {
        filteredScholars: function() {
            return this.scholars
            //filter by department
            .filter(scholar => {
                return this.selectedDepartment == '' || scholar.departments.filter(element => {
                   return element == this.selectedDepartment;
                }).length !== 0
            })
            //filter by subject
            .filter(scholar => {
                return this.selectedSubject == '' || scholar.groups.filter(ele=>{
                    return ele.groupSubject == this.selectedSubject
                }).length !== 0
            })
            //filter by class
            .filter(scholar => {
                return this.selectedClass == 0 || scholar.class== this.selectedClass;
            })
            //filter by group
            .filter(scholar => {
                return this.selectedGroup == '' || scholar.groups.filter(ele=>{
                    return ele.groupCode === this.selectedGroup
                }).length !== 0
            })
        }
    },    
  methods: {
      addScholar() {
          this.addScholarFormVisible=!this.addScholarFormVisible
          document.getElementById('addScholar').setAttribute("disabled", "true")
      },
      eventNewScholar() {
          this.addScholarFormVisible=!this.addScholarFormVisible
          document.getElementById('addScholar').removeAttribute("disabled")
      },
      onChange() {
        this.selectedSubject = ''
        this.selectedGroup = ''
      }
  }
}
</script>
